"use client"

import { useEffect, useState } from "react"
import { apiDashboard } from "@/lib/api-dashboard"
import { useRouter } from "next/navigation"
import { Header } from "@/components/header"
import { Sidebar } from "@/components/sidebar"
import { StatCard } from "@/components/stat-card"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import {
  BarChart,
  Bar,
  LineChart,
  Line,
  PieChart,
  Pie,
  Cell,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts"
import { Package, AlertCircle, ShoppingCart, DollarSign } from "lucide-react"
import { useEstoqueValor, useValorTotalEstoque } from "@/lib/hooks/use-dashboard"

const chartData = [
  { month: "Jan", sales: 4000, profit: 2400 },
  { month: "Fev", sales: 3000, profit: 1398 },
  { month: "Mar", sales: 2000, profit: 9800 },
  { month: "Abr", sales: 2780, profit: 3908 },
  { month: "Mai", sales: 1890, profit: 4800 },
  { month: "Jun", sales: 2390, profit: 3800 },
]

const categoryData = [
  { name: "Eletrônicos", value: 400 },
  { name: "Vestuário", value: 300 },
  { name: "Móveis", value: 300 },
  { name: "Alimentos", value: 200 },
]

const COLORS = ["#2d9d78", "#f44336", "#2196f3", "#ff9800"]

export default function DashboardPage() {
  const router = useRouter()
  const [valorTotal, setValorTotal] = useState('R$ 0,00')
  const [retryCount, setRetryCount] = useState(0)

  const { data: estoqueValor = [], isLoading } = useValorTotalEstoque()

  useEffect(() => {
    const token = localStorage.getItem("token")
    if (!token) {
      router.push("/login")
    }
  }, [router])

  useEffect(() => {
    const fetchValorTotal = async () => {
      try {
        setLoading(true)
        const response = await apiDashboard.get('/estoque/valor-atual')
        const dados = response.data
        
        interface EstoqueItem {
          __root__?: {
            valor_estoque_final: string | number
          }
        }

        const total = dados.reduce((sum: number, item: EstoqueItem) => {
          return sum + (parseFloat(String(item.__root__?.valor_estoque_final)) || 0)
        }, 0)

        const formatado = new Intl.NumberFormat('pt-BR', {
          style: 'currency',
          currency: 'BRL'
        }).format(total)

        setValorTotal(formatado)
      } catch (error) {
        console.error('Erro:', error)
        setValorTotal('R$ 0,00')
      } finally {
        setLoading(false)
      }
    }

    fetchValorTotal()
  }, [])

  return (
    <div className="flex h-screen bg-background">
      <Sidebar />
      <div className="flex-1 flex flex-col overflow-hidden">
        <Header />
        <main className="flex-1 overflow-auto p-6">
          <div className="mb-8">
            <h1 className="text-3xl font-bold">Dashboard</h1>
            <p className="text-muted-foreground">Bem-vindo ao SyStock</p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-8">
            <StatCard title="Valor Total em Estoque" value={valorTotal} icon={DollarSign} trend={12} color="success" />
            <StatCard title="Custo do Estoque" value="R$ 85.320" icon={Package} color="primary" />
            <StatCard title="Produtos com Estoque Baixo" value="12" icon={AlertCircle} color="warning" />
            <StatCard title="Vendas do Dia" value="R$ 8.900" icon={ShoppingCart} trend={5} color="success" />
          </div>

          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 mb-8">
            <Card className="lg:col-span-2">
              <CardHeader>
                <CardTitle>Evolução de Vendas</CardTitle>
              </CardHeader>
              <CardContent>
                <ResponsiveContainer width="100%" height={300}>
                  <LineChart data={chartData}>
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="month" />
                    <YAxis />
                    <Tooltip />
                    <Legend />
                    <Line type="monotone" dataKey="sales" stroke="#2d9d78" />
                    <Line type="monotone" dataKey="profit" stroke="#ff9800" />
                  </LineChart>
                </ResponsiveContainer>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Categorias Vendidas</CardTitle>
              </CardHeader>
              <CardContent>
                <ResponsiveContainer width="100%" height={300}>
                  <PieChart>
                    <Pie
                      data={categoryData}
                      cx="50%"
                      cy="50%"
                      labelLine={false}
                      label={({ name, value }) => `${name} ${value}`}
                      outerRadius={80}
                      fill="#8884d8"
                      dataKey="value"
                    >
                      {categoryData.map((entry, index) => (
                        <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                      ))}
                    </Pie>
                    <Tooltip />
                  </PieChart>
                </ResponsiveContainer>
              </CardContent>
            </Card>
          </div>

          <Card>
            <CardHeader>
              <CardTitle>Produtos Mais Vendidos</CardTitle>
            </CardHeader>
            <CardContent>
              <ResponsiveContainer width="100%" height={300}>
                <BarChart
                  data={[
                    { name: "MacBook Pro", vendas: 120 },
                    { name: "iPhone 15", vendas: 98 },
                    { name: "Monitor 4K", vendas: 87 },
                    { name: "Teclado Mecânico", vendas: 76 },
                    { name: "Mouse Wireless", vendas: 65 },
                  ]}
                >
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis dataKey="name" angle={-45} textAnchor="end" height={100} />
                  <YAxis />
                  <Tooltip />
                  <Bar dataKey="vendas" fill="#2d9d78" />
                </BarChart>
              </ResponsiveContainer>
            </CardContent>
          </Card>
        </main>
      </div>
    </div>
  )
}
